.table__wrapper {
  max-width: 1024px;
  margin: 0 auto;
  padding: 15px;
  box-sizing: border-box; }

.table__tr .table__cell:first-child {
  width: 25%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 10px; }
.table__tr .table__cell:nth-child(2) {
  width: 25%; }
.table__tr .table__cell:nth-child(3) {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto; }

.table__header {
  background-color: var(--color-white-f2);
  display: -webkit-flex;
  display: flex;
  padding: 7px 15px; }
  .table__header .table__cell {
    font-size: 14px;
    color: var(--color-gray);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; }
  .table__header .table__cell:first-child {
    cursor: pointer; }
